import CasefileDataService from "@/services/CasefileDataService";
import moment from "moment";
import axios from "axios";
import { apiConfig } from "@/config/api.config";

export default {
  namespaced: true,
  state: {
    casefiles: [],
    lastFetch: null,
    isLoading: false,
    activeFilters: {},
    filteredCasefiles: [],
    cacheTimeout: 5 * 60 * 1000, // 5 minutes
    currentUserId: null,
    // Add paginated state
    paginated: {
      data: [],
      total: 0,
      page: 1,
      pageSize: 30,
      filters: {},
      isLoading: false,
    },
    paginatedAll: {
      data: [],
      total: 0,
      page: 1,
      pageSize: 30,
      filters: {},
      isLoading: false,
    },
    paginatedPermitted: {
      data: [],
      total: 0,
      page: 1,
      pageSize: 30,
      filters: {},
      isLoading: false,
    },
    paginatedPermittedActive: {
      data: [],
      total: 0,
      page: 1,
      pageSize: 30,
      isLoading: false,
    },
    paginatedPermittedArchive: {
      data: [],
      total: 0,
      page: 1,
      pageSize: 30,
      isLoading: false,
    },
  },

  getters: {
    getFilteredCasefiles:
      (state) => (userRoles, userDepts, userBranches, userId) => {
        // Add validation checks
        if (!userRoles || !userDepts || !userBranches || !userId) {
          return [];
        }

        if (!state.casefiles.length) {
          return [];
        }

        if (!state.filteredCasefiles.length) {
          let filtered = state.casefiles.map((file) => ({
            ...file,
            days: moment().diff(moment(file.dateOfAssign), "days"),
            // Add a computed property for TAT status
            tatStatus: null, // Will be calculated in component
          }));

          // Apply role-based filtering
          if (
            userRoles.includes("MANAGER") ||
            userRoles.includes("ADMIN") ||
            userRoles.includes("CLERK")
          ) {
            state.filteredCasefiles = filtered;
          } else if (
            userRoles.includes("BRANCHCLERK") ||
            userRoles.includes("BRANCHMANAGER")
          ) {
            filtered = filtered.filter(
              (file) =>
                userDepts.includes(file.refType) &&
                userBranches.includes(file.branch)
            );
            state.filteredCasefiles = filtered;
          } else if (userRoles.includes("ADJUSTER")) {
            filtered = filtered.filter(
              (file) =>
                userDepts.includes(file.refType) &&
                userBranches.includes(file.branch) &&
                file.adjuster === userId
            );
            state.filteredCasefiles = filtered;
          }
        }
        return state.filteredCasefiles;
      },

    needsRefresh: (state) => {
      return (
        !state.lastFetch || Date.now() - state.lastFetch > state.cacheTimeout
      );
    },

    getAllCasefiles: (state) => {
      return state.casefiles;
    },
    getPaginatedAll: (state) => state.paginatedAll.data,
    getPaginatedPermitted: (state) => state.paginatedPermitted.data,
    getPaginatedTotalAll: (state) => state.paginatedAll.total,
    getPaginatedTotalPermitted: (state) => state.paginatedPermitted.total,
    getPaginatedPage: (state) => state.paginated.page,
    getPaginatedPageSize: (state) => state.paginated.pageSize,
    getPaginatedLoading: (state) => state.paginated.isLoading,
    // Helper to select which paginated list to use
    getPaginatedCasefilesByMode:
      (state) =>
      (mode = "permitted") => {
        return mode === "all"
          ? state.paginatedAll.data
          : state.paginatedPermitted.data;
      },
    getPaginatedTotalByMode:
      (state) =>
      (mode = "permitted") => {
        return mode === "all"
          ? state.paginatedAll.total
          : state.paginatedPermitted.total;
      },
    getPaginatedPermittedActive: (state) => state.paginatedPermittedActive.data,
    getPaginatedTotalPermittedActive: (state) =>
      state.paginatedPermittedActive.total,
    getPaginatedPermittedActiveLoading: (state) =>
      state.paginatedPermittedActive.isLoading,
    getPaginatedPermittedActivePage: (state) =>
      state.paginatedPermittedActive.page,
    getPaginatedPermittedActivePageSize: (state) =>
      state.paginatedPermittedActive.pageSize,

    getPaginatedPermittedArchive: (state) =>
      state.paginatedPermittedArchive.data,
    getPaginatedTotalPermittedArchive: (state) =>
      state.paginatedPermittedArchive.total,
    getPaginatedPermittedArchiveLoading: (state) =>
      state.paginatedPermittedArchive.isLoading,
  },

  mutations: {
    SET_CASEFILES(state, casefiles) {
      state.casefiles = casefiles;
      state.lastFetch = Date.now();
    },
    SET_LOADING(state, status) {
      state.isLoading = status;
    },
    UPDATE_CASEFILE(state, updatedFile) {
      const index = state.casefiles.findIndex(
        (file) => file.id === updatedFile.id
      );
      if (index !== -1) {
        state.casefiles.splice(index, 1, updatedFile);
      }
    },
    CLEAR_FILTERED_CACHE(state) {
      state.filteredCasefiles = [];
    },
    CLEAR_STORE(state) {
      state.casefiles = [];
      state.lastFetch = null;
      state.filteredCasefiles = [];
      state.currentUserId = null;
      state.isLoading = false; // Add this to ensure loading state is reset
    },
    SET_CURRENT_USER(state, userId) {
      if (state.currentUserId !== userId) {
        state.currentUserId = userId;
        state.casefiles = [];
        state.lastFetch = null;
        state.filteredCasefiles = [];
      }
    },
    UPDATE_TAT_STATUS(state, { id, status }) {
      const file = state.filteredCasefiles.find((f) => f.id === id);
      if (file) {
        file.tatStatus = status;
      }
    },
    SET_PAGINATED_CASEFILES(state, { data, total, page, pageSize, filters }) {
      state.paginated.data = data;
      state.paginated.total = total;
      state.paginated.page = page;
      state.paginated.pageSize = pageSize;
      state.paginated.filters = filters || {};
    },
    SET_PAGINATED_ALL(state, { data, total, page, pageSize, filters }) {
      state.paginatedAll.data = data;
      state.paginatedAll.total = total;
      state.paginatedAll.page = page;
      state.paginatedAll.pageSize = pageSize;
      state.paginatedAll.filters = filters || {};
    },
    SET_PAGINATED_PERMITTED(state, { data, total, page, pageSize, filters }) {
      state.paginatedPermitted.data = data;
      state.paginatedPermitted.total = total;
      state.paginatedPermitted.page = page;
      state.paginatedPermitted.pageSize = pageSize;
      state.paginatedPermitted.filters = filters || {};
    },
    SET_PAGINATED_PERMITTED_ACTIVE(state, payload) {
      state.paginatedPermittedActive.data = payload.data || [];
      state.paginatedPermittedActive.total = payload.total || 0;
      state.paginatedPermittedActive.page = payload.page || 1;
      state.paginatedPermittedActive.pageSize = payload.pageSize || 30;
      state.paginatedPermittedActive.isLoading = false;
    },
    SET_PAGINATED_PERMITTED_ARCHIVE(state, payload) {
      state.paginatedPermittedArchive.data = payload.data || [];
      state.paginatedPermittedArchive.total = payload.total || 0;
      state.paginatedPermittedArchive.page = payload.page || 1;
      state.paginatedPermittedArchive.pageSize = payload.pageSize || 30;
      state.paginatedPermittedArchive.isLoading = false;
    },
    SET_PAGINATED_ALL_LOADING(state, status) {
      state.paginatedAll.isLoading = status;
    },
    SET_PAGINATED_PERMITTED_LOADING(state, status) {
      state.paginatedPermitted.isLoading = status;
    },
    SET_PAGINATED_PERMITTED_ACTIVE_LOADING(state, isLoading) {
      state.paginatedPermittedActive.isLoading = isLoading;
    },
    SET_PAGINATED_PERMITTED_ARCHIVE_LOADING(state, isLoading) {
      state.paginatedPermittedArchive.isLoading = isLoading;
    },
    CLEAR_PAGINATED_CASEFILES(state) {
      state.paginated.data = [];
      state.paginated.total = 0;
      state.paginated.page = 1;
      state.paginated.pageSize = 30;
      state.paginated.filters = {};
      state.paginated.isLoading = false;
    },
    CLEAR_PAGINATED_PERMITTED_ACTIVE(state) {
      state.paginatedPermittedActive = {
        data: [],
        total: 0,
        page: 1,
        pageSize: 30,
        isLoading: false,
      };
    },
    CLEAR_PAGINATED_PERMITTED_ARCHIVE(state) {
      state.paginatedPermittedArchive = {
        data: [],
        total: 0,
        page: 1,
        pageSize: 30,
        isLoading: false,
      };
    },
  },

  actions: {
    async fetchCasefiles({ commit, getters, state }, userId) {
      // Add user check before fetching
      if (state.currentUserId !== userId) {
        commit("SET_CURRENT_USER", userId);
      }

      if (!getters.needsRefresh && state.casefiles.length > 0) return;

      commit("SET_LOADING", true);
      try {
        const response = await CasefileDataService.getAll();
        // Store all cases instead of filtering here
        commit("SET_CASEFILES", response.data);
      } catch (error) {
        console.error("Error fetching casefiles:", error);
        throw error;
      } finally {
        commit("SET_LOADING", false);
      }
    },

    async updateCasefile({ commit }, payload) {
      try {
        const response = await CasefileDataService.update(payload.id, payload);
        commit("UPDATE_CASEFILE", response.data);
        commit("CLEAR_FILTERED_CACHE");
        return response.data;
      } catch (error) {
        console.error("Error updating casefile:", error);
        throw error;
      }
    },

    async fetchPaginatedCasefiles(
      { commit },
      { page = 1, pageSize = 30, filters = {}, noStoreUpdate = false } = {}
    ) {
      commit("SET_PAGINATED_LOADING", true);
      try {
        const params = { page, pageSize, ...filters };
        const response = await CasefileDataService.getPaginated(params);
        if (!noStoreUpdate) {
          commit("SET_PAGINATED_CASEFILES", {
            data: response.data.data,
            total: response.data.total,
            page: response.data.page,
            pageSize: response.data.pageSize,
            filters,
          });
        }
        return response;
      } catch (error) {
        console.error("Error fetching paginated casefiles:", error);
        throw error;
      } finally {
        commit("SET_PAGINATED_LOADING", false);
      }
    },

    async fetchPaginatedAll(
      { commit },
      { page = 1, pageSize = 30, filters = {} } = {}
    ) {
      commit("SET_PAGINATED_ALL_LOADING", true);
      try {
        const params = { page, pageSize, ...filters };
        const response = await CasefileDataService.getPaginated(params);
        commit("SET_PAGINATED_ALL", {
          data: response.data.data,
          total: response.data.total,
          page: response.data.page,
          pageSize: response.data.pageSize,
          filters,
        });
        return response;
      } catch (error) {
        console.error("Error fetching paginated all casefiles:", error);
        throw error;
      } finally {
        commit("SET_PAGINATED_ALL_LOADING", false);
      }
    },
    async fetchPaginatedPermitted(
      { commit, rootState },
      { page = 1, pageSize = 30, filters = {} } = {}
    ) {
      commit("SET_PAGINATED_PERMITTED_LOADING", true);
      try {
        const user = rootState.auth.user;
        if (!user) {
          console.warn(
            "No user found in auth module. Skipping permitted filter."
          );
          commit("SET_PAGINATED_PERMITTED", {
            data: [],
            total: 0,
            page,
            pageSize,
            filters,
          });
          return;
        }
        // Always send user context to backend for permission filtering
        const params = {
          ...filters,
          page,
          pageSize,
          roles: user.roles,
          dept: user.dept,
          branches: user.branches,
          userId: user.id,
        };
        const response = await CasefileDataService.getPaginated(params);
        // No need to filter in frontend, backend already filtered
        commit("SET_PAGINATED_PERMITTED", {
          data: response.data.data,
          total: response.data.total,
          page: response.data.page,
          pageSize: response.data.pageSize,
          filters,
        });
      } catch (error) {
        console.error("Error fetching paginated permitted casefiles:", error);
        throw error;
      } finally {
        commit("SET_PAGINATED_PERMITTED_LOADING", false);
      }
    },
    async fetchPaginatedPermittedActive(
      { commit, rootState },
      { page, pageSize, filters }
    ) {
      commit("SET_PAGINATED_PERMITTED_ACTIVE_LOADING", true);
      try {
        const user = rootState.auth.user;
        const params = {
          ...filters,
          page,
          pageSize,
        };
        if (user) {
          params.roles = user.roles;
          params.dept = user.dept;
          params.branches = user.branches;
          params.userId = user.id;
        }

        // Use the correct baseURL from apiConfig
        const res = await axios.get(
          `${apiConfig.baseURL}/casefiles/paginated-permitted-active`,
          {
            params,
          }
        );
        commit("SET_PAGINATED_PERMITTED_ACTIVE", {
          data: res.data.data,
          total: res.data.total,
          page: res.data.page,
          pageSize: res.data.pageSize,
        });
      } catch (e) {
        commit("SET_PAGINATED_PERMITTED_ACTIVE", {
          data: [],
          total: 0,
          page: 1,
          pageSize: pageSize,
        });
      } finally {
        commit("SET_PAGINATED_PERMITTED_ACTIVE_LOADING", false);
      }
    },
    clearPaginatedPermittedActive({ commit }) {
      commit("CLEAR_PAGINATED_PERMITTED_ACTIVE");
    },

    async fetchPaginatedPermittedArchive(
      { commit, rootState },
      { page, pageSize, filters }
    ) {
      commit("SET_PAGINATED_PERMITTED_ARCHIVE_LOADING", true);
      try {
        const user = rootState.auth.user;
        const params = {
          ...filters,
          page,
          pageSize,
        };
        if (user) {
          params.roles = user.roles;
          params.dept = user.dept;
          params.branches = user.branches;
          params.userId = user.id;
        }
        const res = await axios.get(
          `${apiConfig.baseURL}/casefiles/paginated-permitted-closed`,
          {
            params,
          }
        );
        commit("SET_PAGINATED_PERMITTED_ARCHIVE", {
          data: res.data.data,
          total: res.data.total,
          page: res.data.page,
          pageSize: res.data.pageSize,
        });
      } catch (e) {
        commit("SET_PAGINATED_PERMITTED_ARCHIVE", {
          data: [],
          total: 0,
          page: 1,
          pageSize: pageSize,
        });
      } finally {
        commit("SET_PAGINATED_PERMITTED_ARCHIVE_LOADING", false);
      }
    },
    clearPaginatedPermittedArchive({ commit }) {
      commit("CLEAR_PAGINATED_PERMITTED_ARCHIVE");
    },
    clearStore({ commit }) {
      commit("CLEAR_STORE");
    },
    clearPaginatedCasefiles({ commit }) {
      commit("CLEAR_PAGINATED_CASEFILES");
    },
  },
};
